import { emailRegex } from './emailRegex'

export const checkEmail = (email: string | undefined) => {
	const regex = emailRegex
	if (email === '' || !email) {
		alert('Krlek add meg regisztrlt e-mail cmed')
		return false
	} else if (!regex.test(email)) {
		alert('A megadott e-mail cm nem vals.')
		return false
	} else return true
}

export const checkPassword = (password: string | undefined) => {
	if (password === '' || !password) {
		alert('Krlek adj meg egy jelszt')
		return false
	} else return true
}

export const checkName = (name: string | undefined) => {
	if (name === '' || !name) {
		alert('A nv nem lehet res')
		return false
	} else return true
}
